var that;
export default {
	data() {
		return {
			zhanghao: '',
			xuehao: '',
			zhengjianhaoma: '',
			suozaiquyu: '',
			quyulist: ['西安交通大学', '西北工业大学', '西安音乐学院'],
		}
	},
	onLoad: function(e) {
		that = this;
	},
	onShow: function(res) {

	},
	methods: {
		selectquyu: function() {
			uni.showActionSheet({
				itemList: that.quyulist,
				itemColor: '#838383',
				success: function(res) {
					that.suozaiquyu = that.quyulist[res.tapIndex]
				},
				fail: function(res) {
					console.log(res.errMsg);
				}
			});
		},
		lijidenglu: function() {
			if (!that.zhanghao) {
				uni.showToast({
					title: '请输入账号'
				})

				return
			}
			if (!that.xuehao) {
				uni.showToast({
					title: '请输入学号'
				})

				return
			}
			if (!that.zhengjianhaoma) {
				uni.showToast({
					title: '请输入证件号码'
				})

				return
			}
			if (!that.suozaiquyu) {
				uni.showToast({
					title: '请选择所在区域'
				})

				return
			}
			uni.navigateBack({
				delta: 1
			})

		}
	}

}
